<template>
  <div class="list">
    <div class="chat-list">
      <p v-for="(item,index) in list" :key="index">{{item}}</p>
    </div>
    <div class="send-btn">
      <input v-model="msg" type="text" />
      <button @click="handleSend">发送</button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
      msg: ""
    };
  },
  mounted() {
    this.$socket.emit("chat list");

    this.sockets.subscribe("chat msg", ({ msg, name }) => {
      this.list.push(`${name}说${msg}`);
      console.log(this.list);
    });

    this.sockets.subscribe("chat list", list => {
      this.list = list;
    });
  },
  methods: {
    handleSend() {
      if (!this.msg) {
        return;
      }

      this.$socket.emit("chat msg", {
        msg: this.msg,
        name: localStorage.getItem("name")
      });

      this.msg = "";
    }
  }
};
</script>
<style lang="scss" scoped>
.list{
  height: 100vh;

  .chat-list{
    height: calc(100vh - 30px);
  }

  .send-btn{
    height: 30px;
  }
}
</style>
